import React from "react";
import {Breadcrumb} from "react-bootstrap";

const dataJSON = {
    "__objects": [
        {
            "name": "Books",
            "actions":[
                {
                    "name": "BookDetail",
                    "parameters": [{
                        "name": "id",
                        "widget": "textbox",
                        "type": "int",
                        "default": 1
                    }],
                    "is_allowed": true,
                    "deny_reason": null,
                    "retry_in": 0
                },
                {
                    "name": "BookDelete",
                    "parameters": [{
                        "name": "id",
                        "widget": "textbox",
                        "type": "int",
                        "default": 2

                    }],
                    "is_allowed": false,
                    "deny_reason": "Only Employees can delete books",
                    "retry_in": 0
                },
                {
                    "name": "BookBorrow",
                    "parameters": [{
                        "name": "id",
                        "widget": "textbox",
                        "type": "int",
                        "validators": [
                            {
                                "name": "NotNegative"
                            }
                        ],
                        "is_allowed": true,
                        "deny_reason": null,
                        "retry_in": 0
                    },{
                        "name": "employee_id",
                        "widget": "textbox",
                        "type": "int",
                        "validators": [
                            {
                                "name": "ValidFK"
                            }
                        ],
                        "default": 4
                    }],
                    "data":[
                        {
                            "name": "IDK",
                            "widget": "textbox",
                            "type": "string",
                            "default": "default_tady"
                        }
                    ],
                    "is_allowed": true,
                    "deny_reason": null,
                    "retry_in": 0
                }
            ]
        },
        {
            "name": "Employee",
            "actions":[
                {
                    "name": "EmployeeDetail",
                    "parameters": [{
                        "name": "id",
                        "widget": "textbox",
                        "type": "int"
                    }],
                    "is_allowed": true,
                    "deny_reason": null,
                    "retry_in": 0,
                    "default": 1
                },
                {
                    "name": "EmployeeDelete",
                    "parameters": [{
                        "name": "id",
                        "widget": "textbox",
                        "type": "int"
                    }],
                    "is_allowed": false,
                    "deny_reason": "Employees cannot be deleted for tax reasons",
                    "retry_in": 0,
                    "default": 1
                }
            ]
        }

    ],
    "__actions": {

    }
}

class BreadcrumbMaker extends React.Component{
    render(){
        const path = this.props.location.pathname.split("/").filter(entry => entry.trim() !== '');
        let items = []
        for (let i=1; i <path.length; i++) {
            items.push(
                <Breadcrumb.Item key={i} active={i === path.length-1} href={"../" + path.slice(1,i+1).join("/")}>
                    {path[i]}
                </Breadcrumb.Item>
            )
        }
        return(
            <div>
                <Breadcrumb>
                    {items}
                </Breadcrumb>
            </div>
        )
    }
}

function makeQuery(action, parameters, data){
    let param_string = Object.keys(parameters).map((key) => {
        return key + ": " + JSON.stringify(parameters[key])
    }).join(", ")
    let data_string = Object.keys(data).map((key) => {
        return key + ": " + JSON.stringify(data[key])
    }).join(", ")
    if(data_string !== ""){
        return action + " (" + param_string + ", data: {" + data_string + "})\n{}"
    } else {
        return  action + " (" + param_string + ")\n{}"
    }
}

export {BreadcrumbMaker, dataJSON, makeQuery};