import React from "react";
import {Button, Col, Form, FormControl, ListGroup, Modal, Row, Tab, TabContent, Tabs} from "react-bootstrap";
import {BreadcrumbMaker, dataJSON, makeQuery} from "./Common";

class ObjectList extends React.Component {
    render_item(item_name){
        return(
            <ListGroup.Item action href={"/Frontend/Objects/" + item_name} key={item_name}>
                {item_name}
            </ListGroup.Item>
        )
    }

    render() {
        let items = [];
        dataJSON.__objects.forEach(
            (i) => {
                items.push(this.render_item(i.name));
            })
        return (
            <div>
                <h6 className="mx-3 m-2">Objekty:</h6>
                <ListGroup>
                    {items}
                </ListGroup>
            </div>
        )
    }
}

class ObjectActionsButtons extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            show: null
        };

        this.handleShow = this.handleShow.bind(this);
        this.handleClose = this.handleClose.bind(this);
    }

    handleClose(){
        this.setState({show: null})
    }
    handleShow(id){
        this.setState({show: id})
    }

    handleChangeQuery(e){
        console.log(e.target.value);
    }

    create_modal(action){
        return (
            <Modal show={this.state.show === action.name} onHide={this.handleClose} key={action.name} dialogClassName="query-dialog" contentClassName="query-dialog">
                <ActionModal action={action} />
            </Modal>)
    }

    render_action(action){
        return(
            <Button variant="primary" onClick={() => {this.handleShow(action.name)}} key={action.name} className="m-4">
                {action.name}
            </Button>
        )
    }

    render() {
        const object = this.props.location.pathname.split("/").slice(-1).pop();
        this.buttons = [];
        this.modals = [];
        if(object){
            let action_list = dataJSON.__objects.find(obj => {return obj.name === object});
            if(typeof(action_list) !== "undefined"){
                action_list.actions.forEach((e) => {
                    this.buttons.push(this.render_action(e))
                    this.modals.push(this.create_modal(e))
                })
            }
        }
        return(
            <div>
                {this.buttons}
                {this.modals}
            </div>
        )
    }
}

class ActionModal extends React.Component{
    constructor(props, context) {
        super(props, context);

        const action = this.props.action;
        let param_fields = {};
        let data_fields = {};
        if ("parameters" in action){
            action.parameters.forEach((param) => {param_fields[param.name] = ("default" in param)? param.default : ""})
        }
        if ("data" in action){
            action.data.forEach((data) => {data_fields[data.name] = data.default})
        }

        this.state = {
            action: this.props.action,
            parameters: param_fields,
            data: data_fields,
            query: '',
            response: ''
        };
        this.handleChangeQuery = this.handleChangeQuery.bind(this);
        this.update_response = this.update_response.bind(this);
        this.handleChangeFields = this.handleChangeFields.bind(this)
    }

    create_field(field, type){
        return(
            <Form.Control placeholder={field} key={field} defaultValue={this.state[type][field]} onChange={(e) => {this.handleChangeFields(field, type, e)}} />
        )
    }

    handleChangeFields(caller, type, event){
        let modify_obj = this.state[type];
        modify_obj[caller] = event.target.value
        this.setState({query:makeQuery(this.state.action.name, this.state.parameters, this.state.data)})
    }

    handleChangeQuery(e){
        this.setState({query: e.target.value})
    }

    update_response(){
        this.setState({query: this.state.query, response: this.state.query})
    }

    render(){
        let action = {};
        let param_fields = [];
        let data_fields = [];
        Object.keys(this.state.parameters).forEach((param) => {param_fields.push(this.create_field(param, "parameters"))})
        Object.keys(this.state.data).forEach((data) => {data_fields.push(this.create_field(data, "data"))})
        if(param_fields.length){
            param_fields.unshift(<strong key="param_title">Parameters:</strong>)
        }
        if(data_fields.length){
            data_fields.unshift(<strong key="data_title">Data:</strong>)
        }
        const query = this.state.query;
        const response = this.state.response;
        return(
            <>
                <Modal.Header closeButton>
                    <Modal.Title>{action.name}</Modal.Title>
                </Modal.Header>

                <Modal.Body className="container-fluid">
                    <Form className="container-fluid d-flex flex-column h-100 overflow-hidden p-1">
                        <Row className="flex-grow-1 h-100">
                            <Col className="flex-grow-1 h-100">
                                {param_fields}
                                {data_fields}
                            </Col>
                            <Col className="flex-column pb-1 modal-tabs">
                                <Tabs variant="pills" defaultActiveKey="query" transition={false} className="nav-fill">
                                    <Tab eventKey="query" title="Query:">
                                        <FormControl as="textarea" className="no-resize border-right" value={query} onChange={this.handleChangeQuery}/>
                                    </Tab>
                                    <Tab eventKey="response" title="Response:">
                                        <FormControl readOnly as="textarea" className="no-resize border-right" value={response}/>
                                    </Tab>
                                </Tabs>
                            </Col>
                        </Row>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="primary" onClick={this.update_response}>Save changes</Button>
                </Modal.Footer>
            </>
        )
    }
}


class Objects extends React.Component{
    render() {
        return (
            <Row className="flex-grow-1 overflow-hidden">
                <Col sm={2} className='bg-secondary pl-4 mh-100'>
                    <ObjectList />
                </Col>
                <Col sm={10} className='overflow-auto py-2 mh-100'>
                    <BreadcrumbMaker location={this.props.location} />
                    <ObjectActionsButtons location={this.props.location} />
                </Col>
            </Row>
        );
    }
}

export default Objects;
