import React from 'react';

import {BrowserRouter, Route, Switch} from "react-router-dom";
import Actions from "./Pages/Actions"
import Objects from "./Pages/Objects"
import {Container, Navbar, Nav, Dropdown} from "react-bootstrap";

class TopBar extends React.Component {
    render() {
        let top = "/" + this.props.location.pathname.split("/", 2)[1] + "/";
        return (
            <Navbar variant={"dark"} expand="lg" className={"navbar-expand-lg text-white navbar-expand bg-primary"}>
                <div className="navbar-collapse collapse w-100 order-1 order-md-0 dual-collapse2">
                    <Navbar.Brand href="https://www.github.com/karlosss/simple_api">Simple-API</Navbar.Brand>
                    <div className="collapse navbar-collapse">
                        <Nav variant="pills" activeKey={".." + top}>
                        <Nav.Link href="/Frontend/Objects/">Objects</Nav.Link>
                        <Nav.Link href="/Frontend/Actions/">Actions</Nav.Link>
                    </Nav>
                </div>
            </div>
        <div className="collapse navbar-collapse pe-4">
            <ul className="navbar-nav me-auto ms-auto">
                <Dropdown>
                    <Dropdown.Toggle id="login-dropdown" className="nav-link text-white">
                        Current User
                    </Dropdown.Toggle>

                    <Dropdown.Menu className="dropdown-menu" aria-labelledby="navbarDropdown" alignRight>
                        <Dropdown.Item href="#">Logout Current User</Dropdown.Item>
                        <Dropdown.Divider />
                        <Dropdown.Item href="#">Admin</Dropdown.Item>
                        <Dropdown.Item href="#">Anonymous</Dropdown.Item>
                        <Dropdown.Divider />
                        <Dropdown.Item href="#">Logout All</Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>
            </ul>
        </div>
    </Navbar>
        )
    }
}


const App = () => {
    console.log(window.location)
    return (
        <div>
            <Container fluid className="container-fluid d-flex flex-column vh-100 overflow-hidden p-0">
                <BrowserRouter>
                    <Route path="/" component={TopBar}/>
                    <Switch>
                        <Route path="/Frontend/Actions/" component={Actions} />
                        <Route path="/Frontend/Objects/" component={Objects} />
                    </Switch>
                </BrowserRouter>
            </Container>
        </div>
    )
};

export default App;
